/* Modal Button */
.modalBtn{
  position: fixed;
  top: 40%;
  left: 50%;
  padding: 0.8rem 1.5rem;
  transform: translate(-50%,-50%);
  z-index: -1;
  cursor: pointer;
}

/* Overlay */
.overlay {
  width: 100vw;
  height: 100vh;
  background: rgba(0, 0, 0, 0.5);
}

/* Modal Container */
.modalContainer{
  display: flex;
  max-width: 600px;
  width: 100%;
  position: fixed;
  top: 40%;
  left: 50%;
  transform: translate(-50%,-50%);
  background: white;
  box-shadow: 0 0 1.1rem 0 rgba(0, 0, 0, 0.75);
}

/* Mickey Mouse Image */
.modalContainer > img {
  width: 250px;
  object-fit: cover;
}

.modalRight{
  width: 100%;
}

/* Button for closing modal */
.closeBtn{
  position: fixed;
  top: 0.5rem;
  right: 0.5rem;
  cursor: pointer;
}

.content{
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  margin-top: 3rem;
  padding: 1rem 2rem;
}

.btnContainer{
  display: flex;
  padding: 1rem;
}

.btnContainer > button {
  width: 100%;
  margin: 0.5rem;
  padding: 1rem 0;
  border: 0;
  border-radius: 5px;
  color: #fff;
  cursor: pointer;
}

.btnPrimary{
  background: #092a3b;
}

.btnOutline{
  background: #942021;
}

.bold{
  font-weight: 600;
}

/* For Mobile Responsiveness */
@media only screen and (max-width: 600px){
  .modalContainer{
    flex-direction: column;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    transform: none;
  }

  .modalContainer > img {
    width: 100%;
  }
}